#about-short-info .lg-line {
  line-height: 1.4;
  font-size: 28px;
}

#about-short-info .features-box-icon {
  float: left;
  width: 40px;
  height: 40px;
  padding-top: 6px;
  background: var(--blue-dark);
  border-radius: 50%;
  color: var(--white-normal);
  text-align: center;
}

#about-short-info .features-box-description h3 {
  padding-bottom: 15px;
}

#about-short-info .features-box-description {
  margin-left: 60px;
  margin-bottom: 35px;
}

@media (max-width: 720px) {
  #about-short-info .col-sm-4 {
    flex: 0 0 100%;
    max-width: 100%;
  }

  #about-short-info .col-sm-8 {
    flex: 0 0 100%;
    max-width: 100%;
  }

  #about-short-info .features-box {
    margin-top: 5vh;
  }

  #about-short-info .btn {
    float: right;
  }
}

@media (max-width: 415px) {
  #about-short-info .btn {
    display: none;
  }

  #about-short-info .features-box-icon {
    margin-top: 2vh;
    margin-left: 1vw;
  }
}

@media (max-width: 376px) {
  #about-short-info .features-box-icon {
    margin-top: 5vh;
    margin-left: 1.5vw;
  }
}
